import React, { Component, Fragment } from "react"
import { withRouter, Link } from "react-router-dom"
import classnames from "classnames"
import MetisMenu from "react-metismenu"
import { HCF, HEE } from "@macashipo/mlib"
import { KEventsApp } from "src/helpers/K"
const kSideMenuHCF = "UI_SideMenu"

const genMenuFromItem = item => {
  if (item.Name) {
    let _content = []
    if (item.Children && item.Children.length > 0) {
      for (let i = 0; i < item.Children.length; i++) {
        let _itemChild = genMenuFromItem(item.Children[i])
        if (_itemChild) {
          _content.push(_itemChild)
        }
      }
    }
    return {
      icon: item.Icon || "pe-7s-browser",
      label: item.Name || "",
      to: item.Url || "",
      content: _content,
      externalLink: item.NewWindow === true ? true : false,
    }
  }
}
const getMenuItemMatchWithSearchText = (item, arr = [], textSearch = "") => {
  if (item.Children) {
    item.Children.forEach((v, i) => {
      if (v.Url) {
        let _lowerSearch = textSearch.toLowerCase()
        if (
          v.FilterString.indexOf(_lowerSearch) > -1 ||
          (v.Name && v.Name.toLowerCase().indexOf(_lowerSearch) > -1)
        ) {
          arr.push({
            ...v,
            Icon: "pe-7s-search",
            Children: null,
          })
        }
      }

      if (v.Children) {
        getMenuItemMatchWithSearchText(v, arr, textSearch)
      }
    })
  }
}
const genMenuFromSideMenu = ({
  sidemenuModeSearch,
  sidemenuTextSearch,
} = {}) => {
  var sidemenu = HCF.getCF(kSideMenuHCF)
  console.warn("sidemenu", sidemenu, sidemenuModeSearch, sidemenuTextSearch)
  if (
    sidemenuModeSearch &&
    sidemenuTextSearch &&
    sidemenu &&
    sidemenu.length > 0
  ) {
    let _searchResult = []
    sidemenu.forEach((v, i) => {
      getMenuItemMatchWithSearchText(v, _searchResult, sidemenuTextSearch)
    })
    sidemenu = _searchResult
  }
  if (sidemenu && sidemenu.length > 0) {
    let _arr = []
    for (let i = 0; i < sidemenu.length; i++) {
      let _item = genMenuFromItem(sidemenu[i])
      if (_item) {
        _arr.push(_item)
      }
    }
    return _arr
  }
  return []
}

const DefaultLink = ({
  className,
  classNameActive,
  classNameHasActiveChild,
  active,
  hasActiveChild,
  to,
  externalLink,
  hasSubMenu,
  toggleSubMenu,
  activateMe,
  children,
}) => (
  <Link
    className={classnames(
      className,
      active && classNameActive,
      hasActiveChild && classNameHasActiveChild
    )}
    to={to}
    onClick={hasSubMenu ? toggleSubMenu : activateMe}
    target={externalLink ? "_blank" : undefined}
  >
    {children}
  </Link>
)
class Nav extends Component {
  state = {}
  componentDidMount() {
    console.warn("VerticalNavWrapper componentDidMount")
    HEE.removeListener(KEventsApp.ee_sidemenu_forceupdate) //remove all old event
    HEE.on(KEventsApp.ee_sidemenu_forceupdate, () => {
      console.warn("VerticalNavWrapper forceupdate")
      this.forceUpdate()
    })
  }
  shouldComponentUpdate(nextProps) {
    // console.error("shouldComponentUpdate")
    if (nextProps.sidemenuModeSearch != this.props.sidemenuModeSearch) {
      return true
    }
    if (nextProps.sidemenuTextSearch != this.props.sidemenuTextSearch) {
      return true
    }
    return false
  }
  render() {
    const { sidemenuModeSearch, sidemenuTextSearch } = this.props
    return (
      <Fragment>
        {/* <h5 className="app-sidebar__heading">Menu</h5> */}
        {/* <Link to="dm-deparment">Hello</Link> */}
        {/* <MetisMenu
          content={MainNav}
          activeLinkFromLocation
          className="vertical-nav-menu"
          iconNamePrefix=""
          classNameStateIcon="pe-7s-angle-down"
        />
        <h5 className="app-sidebar__heading">App</h5> */}
        <MetisMenu
          content={genMenuFromSideMenu({
            sidemenuModeSearch,
            sidemenuTextSearch,
          })}
          // activeLinkFromLocation
          className="vertical-nav-menu"
          iconNamePrefix=""
          classNameStateIcon="pe-7s-angle-down"
          LinkComponent={DefaultLink}
        />
      </Fragment>
    )
  }

  isPathActive(path) {
    return this.props.location.pathname.startsWith(path)
  }
}

export default withRouter(Nav)
