import React, { Fragment } from "react"
import { Bounce, toast } from "react-toastify"
import {
  DropdownMenu,
  DropdownToggle,
  Nav,
  NavItem,
  NavLink,
  UncontrolledButtonDropdown,
  ButtonDropdown,
} from "reactstrap"
import { connect } from "react-redux"
import i18next from "i18next"
import { logout, toggleSideNav } from "src/App/App.actions"
import { HCF, HLink, HEE } from "@macashipo/mlib"
import { HAuth } from "src/core/helpers"
import { KAuth } from "src/helpers/Flows/Auth"
import V from "src/helpers/V"
import styled from "styled-components"
import { KConfigApp } from "src/helpers/K"
import { FlowClientClearLocalCache } from "src/helpers/Flows/Client"
// import { faCalendarAlt, faAngleDown } from "@fortawesome/free-solid-svg-icons"
// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
// import avatar1 from "src/assets/images/avatar.jpg"

const WrapAvatar = styled.div`
  display: flex;
  border: 1px solid #e1e4e8;
  border-radius: 30px;
  padding: 2px;
  align-items: center;
  & .a-right {
    text-align: left;
    margin-left: 5px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 100px;
    display: none;
    & .a-name {
      overflow: hidden;
      text-overflow: ellipsis;
      display: inline-block;
      max-width: 100px;
      float: left;
    }
    & .a-pos {
      display: inline-block;
      background: #215383;
      border-radius: 4px;
      padding: 0px 5px;
      color: white;
      float: left;
      clear: both;
    }
    @media (min-width: 768px) {
      display: block;
    }
  }
  @media (min-width: 768px) {
    padding-right: 10px;
  }
`
const kAppMenuHCF = "UI_AppMenu"

const mapStateToProps = state => ({})

const mapDispatchToProps = {
  logout,
  toggleSideNav,
}

const connector = connect(mapStateToProps, mapDispatchToProps)

class UserBoxAvatarName extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      pos: "Admin",
    }
  }
  componentDidMount() {
    HEE.on(KAuth.eeUpdateAuthInfo, this._updateAuthInfo)
  }
  componentWillUnmount() {
    HEE.off(KAuth.eeUpdateAuthInfo, this._updateAuthInfo)
  }
  _updateAuthInfo = () => {
    console.log("Update AuthInfo")
    this.forceUpdate()
  }
  render() {
    const name = HAuth.getUserName()
    const avatar = HAuth.getUserAvatar()
    console.log("render userbox:", name, avatar, this.state.pos)
    let _hasPos = false
    return (
      <WrapAvatar>
        <img
          width={_hasPos ? 42 : 32}
          height={_hasPos ? 42 : 32}
          className="rounded-circle"
          src={avatar}
          style={{ objectFit: "cover" }}
          alt=""
        />
        <div className="a-right">
          <div className="a-name" title={name}>
            {name}
          </div>
          {_hasPos === true && (
            <div className="a-pos" title={this.state.pos}>
              {this.state.pos}
            </div>
          )}
        </div>
      </WrapAvatar>
    )
  }
}
class UserBox extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      active: false,
      isOpen: false,
    }
    console.log("constructor userbox")
  }

  render() {
    const { logout } = this.props
    const appmenu = HCF.getCF(kAppMenuHCF) || []
    let _showClearLocalCache = HCF.getCF(KConfigApp.showClearLocalCache)
    return (
      <>
        <div className="header-btn-lg pr-0">
          <div className="widget-content p-0">
            <div className="widget-content-wrapper">
              <div className="widget-content-left">
                <ButtonDropdown
                  isOpen={this.state.isOpen}
                  toggle={() => {
                    this.setState({ isOpen: !this.state.isOpen })
                  }}
                >
                  <DropdownToggle color="link" className="p-0">
                    <UserBoxAvatarName />
                  </DropdownToggle>
                  <DropdownMenu right className="rm-pointers dropdown-menu-lg">
                    <Nav vertical>
                      {appmenu &&
                        appmenu.length > 0 &&
                        appmenu.map((v, i) => {
                          return (
                            <NavItem key={i}>
                              <NavLink
                                onClick={() => {
                                  console.warn("Click:", v)
                                  if (v.Url) {
                                    HLink.openUrl(
                                      v.Url,
                                      v.NewWindow === true ? "_blank" : "_self"
                                    )
                                  }
                                }}
                              >
                                {v.Name}
                              </NavLink>
                            </NavItem>
                          )
                        })}
                      <NavItem>
                        <NavLink
                          onClick={() => {
                            HLink.openUrl("/my-profile", "_self")
                          }}
                        >
                          {i18next.t("headermenu.Profile")}
                        </NavLink>
                      </NavItem>
                      <NavItem>
                        <NavLink
                          onClick={() => {
                            HLink.openUrl("/setting", "_self")
                          }}
                        >
                          {i18next.t("headermenu.Setting")}
                        </NavLink>
                      </NavItem>
                      <NavItem>
                        <NavLink
                          onClick={() => {
                            V.Api_RefreshCache()
                          }}
                          style={{}}
                        >
                          {i18next.t("headermenu.Refresh Cache")}
                        </NavLink>
                      </NavItem>
                      {_showClearLocalCache !== false && (
                        <NavItem>
                          <NavLink
                            onClick={() => {
                              FlowClientClearLocalCache()
                            }}
                            style={{}}
                          >
                            {i18next.t("headermenu.Clear Local Cache")}
                          </NavLink>
                        </NavItem>
                      )}
                      <NavItem>
                        <NavLink
                          onClick={() => {
                            logout()
                          }}
                          style={{ color: "#992e2e" }}
                        >
                          {i18next.t("headermenu.Logout")}
                        </NavLink>
                      </NavItem>
                    </Nav>
                  </DropdownMenu>
                </ButtonDropdown>
              </div>
              {/* <div className="widget-content-left  ml-3 header-user-info">
                <div className="widget-heading">Alina Mclourd</div>
                <div className="widget-subheading">VP People Manager</div>
              </div> */}
              {/* <div className="widget-content-right header-user-info ml-3">
                <Button
                  className="btn-shadow p-1"
                  size="sm"
                  onClick={this.notify2}
                  color="info"
                  id="Tooltip-1"
                >
                  <i className="fa fa-bug" />
                </Button>
                <UncontrolledTooltip placement="bottom" target={"Tooltip-1"}>
                  Click for Toastify Notifications!
                </UncontrolledTooltip>
              </div> */}
            </div>
          </div>
        </div>
      </>
    )
  }
}

// export default UserBox
export default connector(UserBox)
