import React, { Fragment } from "react"
import cx from "classnames"

import { connect } from "react-redux"

import ReactCSSTransitionGroup from "react-addons-css-transition-group"

import HeaderLogo from "../AppLogo"

// import SearchBox from "./Components/SearchBox"
import UserBox from "./Components/UserBox"
import { HCF, HLink } from "@macashipo/mlib"
import { fnListConfigApp } from "src/helpers/fnList"

class Header extends React.Component {
  render() {
    let {
      headerBackgroundColor,
      enableMobileMenuSmall,
      enableHeaderShadow,
      headerTitle,
    } = this.props
    return (
      <Fragment>
        <ReactCSSTransitionGroup
          component="div"
          className={cx("app-header", headerBackgroundColor, {
            "header-shadow": enableHeaderShadow,
          })}
          transitionName="HeaderAnimation"
          transitionAppear={true}
          transitionAppearTimeout={1500}
          transitionEnter={false}
          transitionLeave={false}
        >
          <HeaderLogo />

          <div
            className={cx("app-header__content", {
              "header-mobile-open": enableMobileMenuSmall,
            })}
          >
            <div className="app-header-left">
              {/* <SearchBox /> */}
              <div className="app-header-left-logo">
                <img
                  alt="Logo"
                  onClick={() => {
                    HLink.push(fnListConfigApp.getHomeUrl())
                  }}
                  src={fnListConfigApp.getAppLogo()}
                  height={"42px"}
                />
              </div>
              {/* Tam thoi an headerTitle */}
              {/* <div className="app-header-left-title">{headerTitle}</div> */}
            </div>
            <div className="app-header-right">
              <UserBox />
            </div>
          </div>
        </ReactCSSTransitionGroup>
      </Fragment>
    )
  }
}

const mapStateToProps = state => ({
  enableHeaderShadow: state.ThemeOptions.enableHeaderShadow,
  closedSmallerSidebar: state.ThemeOptions.closedSmallerSidebar,
  headerBackgroundColor: state.ThemeOptions.headerBackgroundColor,
  enableMobileMenuSmall: state.ThemeOptions.enableMobileMenuSmall,
  headerTitle: state.app.headerTitle,
})

const mapDispatchToProps = dispatch => ({})

export default connect(mapStateToProps, mapDispatchToProps)(Header)
