import React, { Component, Fragment } from "react"
import { connect } from "react-redux"
import cx from "classnames"

import Nav from "../AppNav/VerticalNavWrapper"

import ReactCSSTransitionGroup from "react-addons-css-transition-group"

import PerfectScrollbar from "react-perfect-scrollbar"
import HeaderLogo from "../AppLogo"

import { setEnableMobileMenu } from "src/reducer/ThemeOptions"
import {
  updateModeSearch,
  updateTextSearch,
} from "src/reducer/sidemenu/sidemenu.actions"
class AppSidebar extends Component {
  state = {}

  toggleMobileSidebar = () => {
    let { enableMobileMenu, setEnableMobileMenu } = this.props
    setEnableMobileMenu(!enableMobileMenu)
  }

  render() {
    let {
      backgroundColor,
      enableBackgroundImage,
      enableSidebarShadow,
      backgroundImage,
      backgroundImageOpacity,
      enableHoverSidebar,
      updateModeSearch,
      sidemenuModeSearch,
      sidemenuTextSearch,
    } = this.props

    return (
      <Fragment>
        <div
          className="sidebar-mobile-overlay"
          onClick={this.toggleMobileSidebar}
        />
        <ReactCSSTransitionGroup
          component="div"
          className={cx("app-sidebar", backgroundColor, {
            "sidebar-shadow": enableSidebarShadow,
            "sidebar-expand-hover": enableHoverSidebar,
          })}
          transitionName="SidebarAnimation"
          transitionAppear={true}
          transitionAppearTimeout={1500}
          transitionEnter={false}
          transitionLeave={false}
        >
          <HeaderLogo />
          <PerfectScrollbar>
            <div
              className="app-sidebar__inner"
              onClick={() => {
                console.warn("click")
                if (
                  document.querySelector(".fixed-sidebar.closed-sidebar") &&
                  document.querySelector(".app-sidebar.sidebar-expand-hover") ==
                    null
                ) {
                  document
                    .querySelector(".app-sidebar")
                    .classList.add("sidebar-expand-hover")
                }
              }}
            >
              <Nav
                updateModeSearch={updateModeSearch}
                sidemenuModeSearch={sidemenuModeSearch}
                sidemenuTextSearch={sidemenuTextSearch}
              />
            </div>
          </PerfectScrollbar>
          <div
            className={cx("app-sidebar-bg", backgroundImageOpacity)}
            style={{
              backgroundImage: enableBackgroundImage
                ? "url(" + backgroundImage + ")"
                : null,
            }}
          ></div>
        </ReactCSSTransitionGroup>
      </Fragment>
    )
  }
}

const mapStateToProps = state => ({
  enableBackgroundImage: state.ThemeOptions.enableBackgroundImage,
  enableSidebarShadow: state.ThemeOptions.enableSidebarShadow,
  enableMobileMenu: state.ThemeOptions.enableMobileMenu,
  backgroundColor: state.ThemeOptions.backgroundColor,
  backgroundImage: state.ThemeOptions.backgroundImage,
  backgroundImageOpacity: state.ThemeOptions.backgroundImageOpacity,
  enableHoverSidebar: state.ThemeOptions.enableHoverSidebar,
  sidemenuTextSearch: state.sidemenu.textSearch,
  sidemenuModeSearch: state.sidemenu.modeSearch,
})

const mapDispatchToProps = dispatch => ({
  setEnableMobileMenu: enable => dispatch(setEnableMobileMenu(enable)),
  updateModeSearch: mode => dispatch(updateModeSearch(mode)),
})

export default connect(mapStateToProps, mapDispatchToProps)(AppSidebar)
