import React, { Fragment } from "react"
import { connect } from "react-redux"
import { HCF, HLink } from "@macashipo/mlib"
import Hamburger from "react-hamburgers"
import { fnListConfigApp } from "src/helpers/fnList"
import AppMobileMenu from "../AppMobileMenu"
import styled from "styled-components"
import {
  setEnableClosedSidebar,
  setEnableMobileMenu,
  setEnableMobileMenuSmall,
} from "src/reducer/ThemeOptions"
import {
  updateModeSearch,
  updateTextSearch,
} from "src/reducer/sidemenu/sidemenu.actions"
const WrapHeaderMenu = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 5px;
  & > h5 {
    margin-bottom: 0px;
  }
  & > i {
    color: white;
    font-size: 1rem;
    padding-right: 1rem;
    cursor: pointer;
  }
  & .a-search-container {
    background: white;
    border-radius: 50px;
    padding-left: 10px;
    padding-right: 10px;
    overflow: hidden;
    & > input {
      border: 0px;
      outline: none;
    }
    & > i {
      font-size: 1rem;
      cursor: pointer;
    }
  }
`
class HeaderMenu extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      textSearch: "",
      canSearch: true,
    }
  }
  render() {
    let {
      updateModeSearch,
      updateTextSearch,
      sidemenuModeSearch,
      sidemenuTextSearch,
    } = this.props
    return (
      <WrapHeaderMenu className="app-sidebar-header">
        {sidemenuModeSearch ? (
          <div className="a-search-container">
            <input
              type="text"
              autoFocus
              placeholder="Search..."
              onChange={ev => {
                let _textSearch = ev.currentTarget.value
                this.setState(
                  {
                    textSearch: _textSearch,
                  },
                  () => {
                    updateTextSearch(_textSearch)
                  }
                )
              }}
            />
            <i
              className="fa fa-times"
              onClick={() => {
                this.setState(
                  {
                    textSearch: "",
                    showSearch: false,
                  },
                  () => {
                    updateTextSearch("")
                    updateModeSearch(false)
                  }
                )
              }}
            />
          </div>
        ) : (
          <>
            <h5>Menu</h5>
            {this.state.canSearch && (
              <i
                className="fa fa-search"
                onClick={() => {
                  updateModeSearch(true)
                }}
              />
            )}
          </>
        )}
      </WrapHeaderMenu>
    )
  }
}

class HeaderLogo extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      active: false,
      mobile: false,
      activeSecondaryMenuMobile: false,
    }
  }

  toggleEnableClosedSidebar = () => {
    let { enableClosedSidebar, setEnableClosedSidebar } = this.props
    setEnableClosedSidebar(!enableClosedSidebar)
  }

  state = {
    openLeft: false,
    openRight: false,
    relativeWidth: false,
    width: 280,
    noTouchOpen: false,
    noTouchClose: false,
  }

  render() {
    let { enableClosedSidebar } = this.props

    return (
      <Fragment>
        <div className="app-header__logo">
          <div className="logo-src">
            <img
              alt="Logo"
              onClick={() => {
                HLink.push(fnListConfigApp.getHomeUrl())
              }}
              src={fnListConfigApp.getAppLogo()}
              height={"42px"}
            />
          </div>
          <HeaderMenu {...this.props} />
          <div className="header__pane ml-auto">
            <div onClick={this.toggleEnableClosedSidebar}>
              <Hamburger
                active={enableClosedSidebar}
                type="elastic"
                onClick={() => this.setState({ active: !this.state.active })}
              />
            </div>
          </div>
        </div>
        <AppMobileMenu />
      </Fragment>
    )
  }
}

const mapStateToProps = state => ({
  sidemenuModeSearch: state.sidemenu.modeSearch,
  sidemenuTextSearch: state.sidemenu.textSearch,
  enableClosedSidebar: state.ThemeOptions.enableClosedSidebar,
  enableMobileMenu: state.ThemeOptions.enableMobileMenu,
  enableMobileMenuSmall: state.ThemeOptions.enableMobileMenuSmall,
})

const mapDispatchToProps = dispatch => ({
  updateModeSearch: mode => dispatch(updateModeSearch(mode)),
  updateTextSearch: textSearch => dispatch(updateTextSearch(textSearch)),
  setEnableClosedSidebar: enable => dispatch(setEnableClosedSidebar(enable)),
  setEnableMobileMenu: enable => dispatch(setEnableMobileMenu(enable)),
  setEnableMobileMenuSmall: enable =>
    dispatch(setEnableMobileMenuSmall(enable)),
})

export default connect(mapStateToProps, mapDispatchToProps)(HeaderLogo)
